<template>
    <div class="wrap">
        <div v-if="data && data.length > 0" class="wrap-info" :class="column ? 'flexRow' : ''">
            <template v-for="(item, index) in data">
                <div class="item" :key="index" v-if="!!item.label" :style="{width: column ? '100%' : '50%'}">
                    <span :style="{width: column ? '30%' : '40%'}" class="label" :title="item.label">{{ item.label }}{{ item.label ? '：' : '' }}</span>
                    <span class="message">{{ item.value }}</span>
                </div>
            </template>

        </div>
        <i v-else class="el-icon-loading"></i>
    </div>
</template>

<script>
export default {
    name: 'TextTable',
    props: {
        data: {
            default: []
        }
    },
    data () {
        return {
            column: false
        };
    },
    mounted () {
        if (this.data.length < 9) {
            this.column = true;
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;

        > i {
            /*margin-top: 10vh;*/
            font-size: 48px;
        }
    }

    .wrap-info {
        width: 100%;
        display: flex;
        flex-wrap: wrap;

        .item {
            display: flex;
            align-items: center;
            height: 5vh;

            span {
                display: inline-block;
            }

            .label {
                text-align: right;
                font-size: 20px;
                color: #fff;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap
            }

            .message {
                flex: 1;
                font-size: 20px;
                padding-left: 10px;
                color: #fff;
            }
        }
    }

    .flexRow {
        flex-direction: column;
        justify-content: space-around;
    }
</style>
